<template>
  <div id="registerPage">
    <form @submit.prevent="" class="registerForm p-2" ref="regForm">
      <h2 class="title">FmaxCloud</h2>
      <span class="subtitle">注册一个Fmc账号</span>

      <vs-input v-model.lazy="form.username" primary placeholder="请输入您的用户名" class="mt-3">
        <template #icon>
          <i class="bx bx-user"></i>
        </template>
        <template v-if="!form.username" #message-danger> 必填项 </template>
      </vs-input>

      <vs-input v-model.lazy="form.password" type="password" :visiblePassword="hasVisiblePassword1" @click-icon="hasVisiblePassword1 = !hasVisiblePassword1" icon-after danger placeholder="请输入您的密码" class="mt-3">
        <template #icon>
          <!-- <i class="bx bx-lock-open-alt"></i> -->
          <i v-if="!hasVisiblePassword1" class="bx bx-show-alt"></i>
          <i v-else class="bx bx-hide"></i>
        </template>
        <template v-if="!form.password" #message-danger> 必填项 </template>
      </vs-input>

      <vs-input v-model.lazy="form.confirmPassword" type="password" :visiblePassword="hasVisiblePassword2" @click-icon="hasVisiblePassword2 = !hasVisiblePassword2" icon-after warn placeholder="请确认您的密码" class="mt-3">
        <template #icon>
          <!-- <i class="bx bx-lock-open-alt"></i> -->
          <i v-if="!hasVisiblePassword2" class="bx bx-show-alt"></i>
          <i v-else class="bx bx-hide"></i>
        </template>
        <template v-if="(form.password !== form.confirmPassword) | !form.confirmPassword" #message-danger> 两次密码不一致 </template>
      </vs-input>

      <Vaptcha :passCallback="vpass" ref="vaptchaVc" style="line-height: normal" class="mt-3" />

      <div class="vs-input__message vs-input__message--danger" v-if="!form.verify.token">请完成人机验证</div>

      <vs-checkbox v-model="form.termsType" class="mt-3">我已阅读并接受 <a href="#" class="ms-2">服务条款</a></vs-checkbox>
      <div class="vs-input__message vs-input__message--danger" v-if="!form.termsType">请勾选此选项</div>

      <vs-button size="large" class="mt-3" @click="UserRegister">立即注册</vs-button>
      <hr />

      <button v-wave class="btn btn-info btn-relief" @click="qtReg('qq')">使用QQ登录快速登录/注册</button>
      <button v-wave class="btn btn-success btn-relief" @click="qtReg('wx')">使用微信快速登录/注册</button>
      <span class="regspan">已有账号？ <router-link to="/auth/login">前往登录</router-link> 或 <router-link to="/auth/forgot">忘记密码？</router-link></span>
      <span class="retrieve">如果您找不回账号,可以<a href="#">咨询客服</a>协助找回</span>
    </form>
  </div>
</template>

<script>
import Vaptcha from "@/components/Vaptcha.vue";
import { reg } from "@/scripts/auth";
import _ from "lodash";

export default {
  name: "Regiser",
  data() {
    return {
      form: {
        username: "",
        password: "",
        verify: {
          server: "",
          token: "",
        },
        confirmPassword: "",
        termsType: false,
      },
      hasVisiblePassword1: false,
      hasVisiblePassword2: false,
    };
  },
  components: {
    Vaptcha,
  },
  mounted() {},
  methods: {
    vpass(data) {
      this.form.verify.server = data.server;
      this.form.verify.token = data.token;
    },
    qtReg() {
      // this.$toastx({
      //     title: '暂不支持',
      //     type: 'warning'
      // })
    },
    UserRegister() {
      if (!this.form.username || !this.form.password || !this.form.confirmPassword || this.form.password !== this.form.confirmPassword || !this.form.termsType) {
        this.$toastx({
          title: "请填写完整信息",
          type: "warning",
        });
        return;
      }

      if (!this.form.verify.token) {
        this.$toastx({
          title: "请先完成人机验证",
          type: "warning",
        });
        return;
      }

      const loading = this.$vs.loading({
        target: this.$refs.regForm,
        text: "正在注册",
      });

      const original = {
        uname: this.form.username,
        nickname: this.form.username,
        pwd: this.form.password,
        verify: {
          sr: this.form.verify.server,
          tk: this.form.verify.token,
        },
      };

      reg(original).then((resp) => {
        if (!resp) {
          loading.close();
          this.resetVerify();
        }
      });
    },
    resetVerify() {
      this.$refs.vaptchaVc.reset();
      this.form.verify.server = "";
      this.form.verify.token = "";
    },
  },
};
</script>

