.btn {
  padding: 10px;
}
.registerForm span {
  font-size: 14px;
}
.registerForm {
  display: flex;
  flex-direction: column;

  .input {
    margin-left: 5px;
    margin-bottom: 5px;
    width: 300px;
  }
  .title {
    text-align: center;
    margin-bottom: 20px;
  }
  .subtitle {
    text-align: center;
    font-size: 18px;
    margin-bottom: 20px;
  }
  .regspan {
    margin-top: 40px;
    text-align: center;
  }
  .retrieve {
    text-align: center;
    color: #6e6b7b;
  }
  .btn {
    margin-bottom: 8px;
  }
}
.registerForm .btn:first-of-type {
  margin-top: 20px;
  margin-bottom: 0px;
}

@media screen and (min-width: 576px) {
  #registerPage {
    min-width: 450px !important;
  }
}
</style>
